
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 144rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    margin: 0 auto;

}

.nav {
    display: flex;
    align-items: center;
    list-style: none;
    padding-left: 0;
    &-item {
        margin: 0 2.5rem;
    }
    &-link {
        text-decoration: none;
        font-weight: 700;
        color: rgba($white, 0.5);
        transition: 0.2s ease color;
        &:hover {
            color: rgba($white, 1);
        }
        &::after{
            content: '';
            opacity: 0;
            display: block;
            width: 100%;
            height: .2rem;
            margin-top: .5rem;
            border-radius: 5rem;
            background: $bg-gradient-safe;
            background: $bg-gradient;     
        }
    &.active {
        color: rgba($white, 1);
        &::after {
            opacity: 1;
        }
    }
  }
}
.control {
    display: flex;
    align-items: center;
    list-style: none;
    padding-left: 0;
    &-item {
        margin-left: 7.5rem;
    }
    &-btn {
        background-color: transparent;
        border: none;
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        &_search {
            background-image: url('../images/search.svg');
            width: 1.8rem;
            height:1.8rem;
        }
        &_notice {
            background-image: url('../images/notice.svg');
            width: 2.13rem;
            height:2.35rem;
        }
        &_account {
            background-image: url('../images/account.svg');
            width: 1.8rem;
            height:1.92rem;
        }

            
    }
}
